<template>
  <div id="card-detail">
      <Card>
        <template #title>
          {{ product.name }}
        </template>
        <template #header>
          <img :src="imgUrl">
        </template>
        <template #content>
            {{product.desc}}
        </template>
        <template #footer> 
            <AddToCart :product="product"/>
        </template>
      </Card>
  </div>
</template>

<script>
import AddToCart from './AddToCart.vue';

export default {
  name: "ProductDetail",
  components: {
    AddToCart
  },
  props: {
    id: String,
  },
  data() {
    return {
      product: this.$store.getters['products/getProductById'](this.id),
      imgUrl: this.$store.getters['products/getImgUrl'](this.id),
    };
  }
};
</script>

<style scoped>
  #card-detail {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  #card-detail .p-card {
    width: 30em;
  }

  #card-detail .p-card-header img {
    max-height: 30em;
  }
</style>
