<template>
    <div class="">
        <i class="pi pi-times product-remove-cart" @click="removeFromCart(product)"></i>
    </div>
</template>
<script>

export default {
    name : "RemoveFromCart",
    props : {
        product : Object
    },
    data(){
        return {
        }
    },
    methods: {
        removeFromCart(product) {
            this.$store.dispatch('cart/removeProduct', product.id);
        }
    }
}
</script>